import React, {Component} from "react";
import {OraModal, OraButton} from "eshop/components/OraCommonComponents";

class MultiCartRemovePopup extends Component {

    constructor(props) {
        super(props);
    }

    substituteElementName(text, substitution) {
        return text ? text.replace('$', substitution) : text;
    }

    render() {
        return (
            <OraModal id={ this.props.id } showClose={true} narrow={true} fallback={ this.props.onClickRemove }>
                <div className="l-page-row u-margin-l">
                    <div className="l-row u-spacing">
                        <div className="l-col l-col-12">
                            <h2>{ this.props.deleteEntryPopupTitle || this.props.sectionConf.deleteEntryPopupTitle }</h2>
                        </div>
                    </div>
                    <div className="l-row u-spacing-xl">
                        <div className="l-col l-col-12">
                            <p>{ this.substituteElementName(this.props.deleteEntryPopupContent || this.props.sectionConf.deleteEntryPopupContent, this.props.textRepresentation) }</p>
                        </div>
                    </div>
                    <div className="l-row">
                        <div className="l-col l-col-small-12 l-col-medium-4 l-col-4">
                            <OraButton className="u-w-100" modal={true} type="secondary" >
                                { this.props.declineDeleteEntryPopupButtonLabel || this.props.sectionConf.declineDeleteEntryPopupButtonLabel }
                            </OraButton>
                        </div>
                        <div className="l-col l-col-small-12 l-col-medium-4 l-col-4">
                            <OraButton onClick={ this.props.onClickRemove } className="u-w-100" modal={true}>
                                { this.props.confirmDeleteEntryPopupButtonLabel || this.props.sectionConf.confirmDeleteEntryPopupButtonLabel }
                            </OraButton>
                        </div>
                    </div>
                </div>
            </OraModal>
        )
    }

}

MultiCartRemovePopup.defaultProps = {
    sectionConf: {
        deleteEntryPopupTitle: "Usunięcie elementu",
        deleteEntryPopupContent: "Usuwasz element $. Jesteś pewny?",
        declineDeleteEntryPopupButtonLabel: "Anuluj",
        confirmDeleteEntryPopupButtonLabel: "Zmień"
    }
};

export default MultiCartRemovePopup;
